.footer {
	background-color: black;
	color: #fff;
	position: absolute;
	width: 100%;
	height: 35px;
	bottom: 0;
}

.div-only-mobile {
	display: none;
}

.div-no-mobile {}

@media screen and (max-width: 849px) {
	.div-no-mobile {
		display: none;
	}
	.div-only-mobile {
		display: block;
	}
}

.line {
	overflow: hidden;
	position: relative;
}

.sm-icon {
	padding: 0.5em;
}

.name-of-blog {
	padding-left: 0.4em;
	/* LEFT WITH INTENTION OF FUTURE IDEAS ABOUT DESIGN  */
}

.video-container {
	position: relative;
	width: 100%;
	padding-bottom: 56.25%;
}

.video-embed-div {
	padding-top: 5%;
	/*padding-bottom: 5%;*/
}

.video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 0;
	border-radius: 5%;
	  overflow: hidden;
	  z-index: 1;
}

.line::after {
	content: '';
	width: 100%;
	box-sizing: border-box;
	border-top: 1px solid gray;
	position: absolute;
	bottom: 0.5em;
	margin-left: 0.5em;
}

